"use client";

import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence, useScroll, useTransform } from 'framer-motion';
import {
  ArrowRight,
  Menu,
  X,
  Linkedin,
  Twitter,
  Facebook,
  Instagram,
  ArrowUp
} from 'lucide-react';
import dynamic from 'next/dynamic';

const Spline = dynamic(() => import('@splinetool/react-spline'), {
  ssr: false,
  loading: () => <div className="w-full h-full flex items-center justify-center text-gray-400">Loading 3D Scene...</div>,
});



// --- Components ---

const Button = ({ children, variant = 'primary', className = '', ...props }: React.ButtonHTMLAttributes<HTMLButtonElement> & { variant?: 'primary' | 'outline' | 'ghost' }) => {
  const baseStyles = "px-8 py-4 rounded-full font-semibold transition-all duration-300 flex items-center gap-2 group";
  const variants = {
    primary: "bg-[#0A66C2] text-white hover:bg-[#0077FF] hover:shadow-lg hover:shadow-blue-500/30 hover:-translate-y-1",
    outline: "border-2 border-[#0A66C2] text-[#0A66C2] hover:bg-[#0A66C2] hover:text-white",
    ghost: "text-[#0A66C2] hover:text-[#0077FF] p-0 bg-transparent"
  };

  return (
    <button className={`${baseStyles} ${variants[variant]} ${className}`} {...props}>
      {children}
      {variant !== 'ghost' && <ArrowRight className="w-4 h-4 group-hover:translate-x-1 transition-transform" />}
    </button>
  );
};

// --- Main Page Component ---

export default function Home() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [showBackToTop, setShowBackToTop] = useState(false);

  const { scrollY } = useScroll();
  const heroOpacity = useTransform(scrollY, [0, 400], [1, 0]);
  const heroY = useTransform(scrollY, [0, 400], [0, -50]);
  const heroScale = useTransform(scrollY, [0, 400], [1, 0.95]);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
      setShowBackToTop(window.scrollY > 500);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.6, ease: [0.22, 1, 0.36, 1] as any },
    },
  };

  return (
    <div className="min-h-screen bg-[#F7F9FB] text-[#0B1320] font-sans selection:bg-[#0A66C2]/20 selection:text-[#0A66C2] flex flex-col relative">

      {/* Fixed Right Side Blue Background with Gradient Merge */}
      <div className="fixed top-0 right-0 w-full h-full bg-gradient-to-l from-[#0A66C2] from-50% to-transparent z-0 hidden lg:block pointer-events-none" />

      {/* Fixed 3D Background - Right Side on Desktop */}
      <div className="fixed top-0 right-0 w-full lg:w-1/2 h-full z-0 pointer-events-none lg:pointer-events-auto">
        <Spline
          scene="https://prod.spline.design/eyHeYtJ44-VfgdeO/scene.splinecode"
          className="w-full h-full"
        />
      </div>

      {/* Navigation */}
      <nav className={`fixed w-full z-50 transition-all duration-500 ${isScrolled ? 'bg-white/80 backdrop-blur-xl shadow-sm py-4' : 'bg-transparent py-6'}`}>
        <div className="container mx-auto px-6 flex justify-between items-center">
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.5 }}
            className="text-2xl font-bold text-[#0B1320]"
          >
            Creat<span className="text-[#0A66C2]">zion</span>
          </motion.div>

          {/* Desktop Nav */}
          <div className="hidden md:flex items-center gap-8">
            <Button variant="primary" className="px-6 py-2 text-sm">Contact Us</Button>
          </div>

          {/* Mobile Menu Button */}
          <button className="md:hidden text-[#0B1320]" onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
            {isMobileMenuOpen ? <X /> : <Menu />}
          </button>
        </div>

        {/* Mobile Menu */}
        <AnimatePresence>
          {isMobileMenuOpen && (
            <motion.div
              initial={{ height: 0, opacity: 0 }}
              animate={{ height: 'auto', opacity: 1 }}
              exit={{ height: 0, opacity: 0 }}
              className="md:hidden bg-white border-b border-gray-100 overflow-hidden shadow-lg"
            >
              <div className="flex flex-col p-6 gap-4">
                <Button variant="primary" className="w-full justify-center">Contact Us</Button>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </nav>

      {/* Hero Section */}
      <section className="relative min-h-screen flex flex-col justify-center pt-28 pb-20 overflow-hidden pointer-events-none">
        {/* Background Elements */}
        <div className="absolute inset-0 bg-gradient-to-r from-[#F7F9FB]/90 via-[#F7F9FB]/50 to-transparent -z-10" />

        <div className="container mx-auto px-6 relative z-10">
          <div className="flex flex-col lg:flex-row items-center mb-20 pointer-events-auto">

            {/* Text Content - Left Side with Parallax */}
            <motion.div
              style={{ opacity: heroOpacity, y: heroY, scale: heroScale }}
              className="w-full lg:w-1/2 text-center lg:text-left"
            >
              <motion.span
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.2, duration: 0.6 }}
                className="inline-block py-1 px-3 rounded-full bg-[#0A66C2]/10 text-[#0A66C2] text-sm font-bold mb-6 backdrop-blur-sm uppercase tracking-widest"
              >
                Innovating for the Future
              </motion.span>
              <motion.h1
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.3, duration: 0.6 }}
                className="text-6xl md:text-8xl lg:text-9xl font-black text-[#0B1320] mb-8 tracking-tighter leading-[0.9] uppercase"
              >
                Building <br />
                <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#0A66C2] to-[#A100FF]">The Future</span>
              </motion.h1>
              <motion.p
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.4, duration: 0.6 }}
                className="text-xl text-gray-600 max-w-xl mx-auto lg:mx-0 mb-10 leading-relaxed font-medium"
              >
                We help global organizations navigate complexity and accelerate growth through world-class technology solutions and strategic consulting.
              </motion.p>
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.5, duration: 0.6 }}
                className="flex flex-col sm:flex-row gap-4 justify-center lg:justify-start items-center"
              >
                <Button variant="primary">Explore Services</Button>
                <Button variant="outline">View Case Studies</Button>
              </motion.div>
            </motion.div>

            {/* Spacer for 3D Model on Desktop */}
            <div className="hidden lg:block w-1/2" />
          </div>

          {/* Metrics */}
          <motion.div
            variants={containerVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, margin: "-50px" }}
            className="grid grid-cols-2 md:grid-cols-4 gap-8 border-t border-gray-200/50 pt-10 pointer-events-auto max-w-4xl lg:max-w-none"
          >
            {[
              { label: "Years Experience", value: "25+" },
              { label: "Global Clients", value: "500+" },
              { label: "Projects Delivered", value: "2k+" },
              { label: "Team Members", value: "1200+" },
            ].map((stat, index) => (
              <motion.div
                key={index}
                variants={itemVariants}
                className="text-center lg:text-left group cursor-default relative pl-4"
              >
                <div className="absolute left-0 top-0 bottom-0 w-1 bg-[#A100FF] scale-y-0 group-hover:scale-y-100 transition-transform duration-300 origin-bottom" />
                <div className="text-3xl md:text-4xl font-bold text-[#0B1320] mb-2 group-hover:text-[#A100FF] transition-colors duration-300">{stat.value}</div>
                <div className="text-sm text-gray-500 font-semibold uppercase tracking-wider group-hover:text-[#0B1320] transition-colors">{stat.label}</div>
              </motion.div>
            ))}
          </motion.div>
        </div>

        {/* Scroll Indicator */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 1, duration: 1 }}
          className="absolute bottom-8 left-1/2 -translate-x-1/2 text-[#A100FF] animate-bounce hidden lg:block"
        >
          <ArrowUp className="w-6 h-6 rotate-180" />
        </motion.div>
      </section>

      {/* Capabilities Section (Accenture-style Grid) */}
      <section className="py-24 bg-white relative z-10">
        <div className="container mx-auto px-6">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            className="mb-16"
          >
            <h2 className="text-5xl md:text-7xl font-black text-[#0B1320] mb-6 uppercase tracking-tighter">360° Value</h2>
            <div className="w-20 h-1.5 bg-gradient-to-r from-[#0A66C2] to-[#A100FF]" />
          </motion.div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {[
              { title: "Strategy & Consulting", desc: "Reinventing business with data and AI." },
              { title: "Technology", desc: "Powering innovation with cloud and platforms." },
              { title: "Operations", desc: "Transforming processes for intelligent operations." },
              { title: "Industry X", desc: "Digital engineering and manufacturing." },
              { title: "Song", desc: "Accelerating growth through relevance." },
              { title: "Sustainability", desc: "Embedding value in every decision." },
            ].map((item, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ delay: index * 0.1 }}
                className="group p-8 border border-gray-100 bg-[#F7F9FB] hover:bg-white hover:shadow-2xl hover:shadow-purple-500/10 transition-all duration-500 rounded-xl cursor-pointer relative overflow-hidden"
              >
                <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-[#0A66C2] to-[#A100FF] transform scale-x-0 group-hover:scale-x-100 transition-transform duration-500 origin-left" />
                <h3 className="text-2xl font-bold text-[#0B1320] mb-4 group-hover:text-[#A100FF] transition-colors flex items-center justify-between">
                  {item.title}
                  <ArrowRight className="w-6 h-6 opacity-0 -translate-x-4 group-hover:opacity-100 group-hover:translate-x-0 transition-all duration-300 text-[#A100FF]" />
                </h3>
                <p className="text-gray-600 leading-relaxed">{item.desc}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Featured Insights (Large Cards) */}
      <section className="py-24 bg-[#0B1320] text-white relative z-10 overflow-hidden">
        <div className="absolute inset-0 bg-[url('https://grainy-gradients.vercel.app/noise.svg')] opacity-20" />
        <div className="container mx-auto px-6 relative">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            className="mb-16 flex flex-col md:flex-row justify-between items-end gap-6"
          >
            <div>
              <h2 className="text-5xl md:text-7xl font-black mb-6 uppercase tracking-tighter">Featured Insights</h2>
              <div className="w-20 h-1.5 bg-gradient-to-r from-[#A100FF] to-[#0A66C2]" />
            </div>
            <Button variant="outline" className="border-white text-white hover:bg-white hover:text-[#0B1320]">View All Insights</Button>
          </motion.div>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {[
              { title: "The Life Trends 2025", category: "Research", color: "from-purple-600 to-blue-600" },
              { title: "Reinventing with Gen AI", category: "Technology", color: "from-blue-600 to-cyan-600" },
            ].map((item, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, scale: 0.95 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                className="group relative h-[400px] rounded-2xl overflow-hidden cursor-pointer"
              >
                {/* Background Gradient Placeholder (Replace with Image) */}
                <div className={`absolute inset-0 bg-gradient-to-br ${item.color} opacity-80 group-hover:scale-110 transition-transform duration-700`} />
                <div className="absolute inset-0 bg-black/20 group-hover:bg-black/40 transition-colors duration-500" />

                <div className="absolute bottom-0 left-0 p-10 w-full">
                  <div className="text-sm font-bold uppercase tracking-widest mb-3 opacity-80">{item.category}</div>
                  <h3 className="text-3xl md:text-4xl font-bold mb-6 group-hover:-translate-y-2 transition-transform duration-300">{item.title}</h3>
                  <div className="h-0 overflow-hidden group-hover:h-auto transition-all duration-300">
                    <span className="inline-flex items-center gap-2 text-lg font-semibold border-b border-white pb-1">
                      Read Report <ArrowRight className="w-5 h-5" />
                    </span>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-[#0B1320] text-white pt-20 pb-10 mt-auto relative z-10">
        <div className="container mx-auto px-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 mb-16">
            <div>
              <div className="text-2xl font-bold text-white mb-6">Creat<span className="text-[#0A66C2]">zion</span></div>
              <p className="text-gray-400 mb-6">
                Empowering enterprises with next-generation technology solutions.
              </p>
              <div className="flex gap-4">
                {[Linkedin, Twitter, Facebook, Instagram].map((Icon, i) => (
                  <a key={i} href="#" className="w-10 h-10 rounded-full bg-white/10 flex items-center justify-center text-gray-400 hover:bg-[#0A66C2] hover:text-white transition-all">
                    <Icon className="w-5 h-5" />
                  </a>
                ))}
              </div>
            </div>

            <div></div> {/* Spacer */}

            <div>
              <h4 className="text-white font-bold mb-6">Company</h4>
              <ul className="space-y-4 text-gray-400">
                {['Contact'].map((item) => (
                  <li key={item}><a href="#" className="hover:text-[#0A66C2] transition-colors">{item}</a></li>
                ))}
              </ul>
            </div>

            <div>
              <h4 className="text-white font-bold mb-6">Newsletter</h4>
              <p className="text-gray-400 mb-4">Subscribe to our newsletter for the latest updates.</p>
              <div className="flex gap-2">
                <input type="email" placeholder="Email address" className="bg-white/10 border border-white/20 rounded-lg px-4 py-2 text-white w-full focus:outline-none focus:border-[#0A66C2]" />
                <button className="bg-[#0A66C2] text-white px-4 rounded-lg hover:bg-[#0077FF] transition-colors">
                  <ArrowRight className="w-5 h-5" />
                </button>
              </div>
            </div>
          </div>

          <div className="border-t border-white/10 pt-8 flex flex-col md:flex-row justify-between items-center gap-4">
            <div className="text-gray-500 text-sm">
              © 2024 Creatzion. All rights reserved.
            </div>
            <div className="flex gap-8 text-sm text-gray-500">
              <a href="#" className="hover:text-white transition-colors">Privacy Policy</a>
              <a href="#" className="hover:text-white transition-colors">Terms of Service</a>
            </div>
          </div>
        </div>
      </footer>

      {/* Back to Top */}
      <AnimatePresence>
        {showBackToTop && (
          <motion.button
            initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0 }}
            onClick={scrollToTop}
            className="fixed bottom-8 right-8 w-12 h-12 bg-[#0A66C2] text-white rounded-full shadow-lg flex items-center justify-center hover:bg-[#0077FF] transition-colors z-40"
          >
            <ArrowUp className="w-6 h-6" />
          </motion.button>
        )}
      </AnimatePresence>
    </div>
  );
}
